import React from 'react';
import './Contact.scss';
import ContactImage from './contact.png'
import { ReactComponent as PhoneIcon } from './phone-icon.svg';
import { ReactComponent as EmailIcon } from './email-icon.svg';


export default function Contact() {
    return (
        <div className="page contact">
            <div className="bg-light-arc" />
            <img src={ContactImage} alt="" className="image" />
            <div className="info-container">
                <div className="header">با ما در تماس باشید</div>
                <div className="header-line" />
                <div className="gap" />
                <div className="text-group">
                    <PhoneIcon className="icon" />
                    <div className="title">شماره تماس</div>
                    {/* <div className="value">۹۸۹۰۵۹۲۵۰۲۵۴+</div> */}
                    <div className="value">09059250254</div>
                </div>
                <div className="gap" />
                <div className="text-group">
                    <EmailIcon className="icon" />
                    <div className="title">پست الکترونیکی</div>
                    <div className="value">programmers24.ir@gmail.com</div>
                </div>
            </div>
        </div>
    );
}

