import React, { useCallback, useState } from 'react';
import './Team.scss';
import { ReactComponent as MohsenImage } from './mohsen.svg';
import { ReactComponent as AliImage } from './ali.svg';
import { ReactComponent as SarahImage } from './sara.svg';
import CheckImage from './Check.svg'


export default function Team() {

    const SaraName = "سارا";
    const [isMoreSara , setMoreSara] = useState(false);
    const onSaraClick = useCallback( () => { setMoreSara(!isMoreSara); } , [isMoreSara] );
    const infoClassSara = (isMoreSara ?  "Rectangle-Large" : "sara_area Rectangle");

    const [isMoreAli , setMoreAli] = useState(false);
    const onAliClick = useCallback( () => { setMoreAli(!isMoreAli); } , [isMoreAli] );
    const infoClassAli = (isMoreAli ?  "Rectangle-Large" : "ali_area Rectangle");

    const [isMoreMohsen , setMoreMohsen] = useState(false);
    const onMohsenClick = useCallback( () => { setMoreMohsen(!isMoreMohsen); } , [isMoreMohsen] );
    const infoClassMohsen = (isMoreMohsen ?  "Rectangle-Large" : "mohsen_area Rectangle");

    const infoClass =(isMoreSara | isMoreAli | isMoreMohsen ? "grid-container-more" : "grid-container-team" );

    // const checkSara = (isMoreAli | isMoreMohsen ? "delate_text_sara" : "");

    // <div className="text-more"><img src={CheckImage} alt=""/></div>
                     
    return (
        <div className="page team">
            <div className="bg-circles pos-right">
                <div className="inner-circle" />
            </div>

        <div className={infoClass}>

            {/* <div className="grid-container-team"> */}

                <div className={infoClassSara}>
                {/* <div className="sara_area Rectangle"> */}

                {isMoreAli || isMoreMohsen ? "" : (<div>

                    {isMoreSara? (<div>

                        <div class="outer-container"> <div class="inner-container"> <div class="element">

                        <SarahImage className="image-more" />
                        <div className="title-more-sara">سارا آسمان منظر</div>
                        <div className="title-more2-sara">کارشناسی ارشد مهندسی فناوری اطلاعات</div>
                        <div className="text-more"><br /></div>
                        <div className="text-more"><img src={CheckImage} alt=""/>رتبه اول فارغ التحصیلان دوره کارشناسی رشته مهندسی فناوری اطلاعات، دانشگاه صنعتی خواجه‌نصیر‌الدین‌طوسی</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>پایان نامه کارشناسی ارشد با تمرکز بر داده کاوی</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>پایان نامه کارشناسی با موضوع مدارس و دانشگاه های هوشمند</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>همکاری با واحد کامپیوتر پژوهشگاه صنعت نفت و آموزش و پرورش</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>توسعه دهنده نرم افزار های تحت موبایل و وب سایت های پویا</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>پژوهش و چاپ مقالات متعدد در زمینه های داده کاوی، مدیریت دانش، تکنیک های تست نرم افزار و مباحث امنیتی مرتبط</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>دارای تجربه در حوزه مدیریت ارتباط با مشتری و مهندسی نیازمندی ها و ارائه تحقیقات چاپ شده در زمینه های مذکور</div>
                        <div className="text-more"><br /></div>
                        <div className="ovalButton-more Sara" onClick={onSaraClick}>بازگشت به صفحه اصلی</div>

                        </div> </div> </div>

                     </div>) : (<div>

                        <SarahImage className="images-team" />
                        <div className="text-team-name one">سارا</div>
                        <div className="text-team-name two">آسمان منظر</div>
                        <div className="text-team"><br /></div>
                        <div className="text-team text1" style={{color: "#527a7a"}}>عضو هسته اصلی</div>
                        <div className="text-team"><br /></div>
                        <div className="text-team text2">کارشناسی ارشد</div>
                        <div className="text-team text3">مهندسی فناوری اطلاعات</div>
                        <div className="text-team text4">مدیریت سیستم های اطلاعاتی</div>
                        {/* <div className="ovalButton Sara">اطلاعات بیشتر</div> */}
                        <div className="ovalButton Sara" onClick={onSaraClick}>اطلاعات بیشتر
                        {/* {isMoreSara? (<p> {SaraName} </p>) : ""} */}
                        </div>

                     </div>)}

                </div>)}

                </div>

                <div className={infoClassAli}>
                {/* <div className="ali_area Rectangle"> */}

                {isMoreSara || isMoreMohsen ? "" : (<div>

                    {isMoreAli? (<div>

                        <div class="outer-container"> <div class="inner-container"> <div class="element">

                        <AliImage className="image-more" />
                        <div className="title-more-ali">علی  محمدحسینی</div>
                        <div className="title-more2-ali">دکتری مدیریت منابع انسانی</div>
                        <div className="text-more"><br /></div>
                        <div className="text-more"><img src={CheckImage} alt=""/>عضو موسس، مدیر پروژه و معمار سیستم در تیم پژوهشی گروه مهندسی آهو در دانشگاه آزاد اسلامی</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>عضو تیم توسعه پروژه های اختصاصی سازمانهای انرژی اتمی، ثبت احوال و برخی دانشگاه ها</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>عضو هیات مدیره، مدیر امور مالی-اداری و قراردادها در شرکت گروه مشاورین آهو پیشرو</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>برنامه ریزی، طراحی، اجرا و مدیریت سامانه های فنی و چارچوبهای اجرایی در برخی سامانه های قوه قضاییه</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>پژوهشگر اصلی یا همکار طرح در پژوهشهای بین رشته ای و مدیریتی پژوهشکده مطالعات پایه قوه قضاییه</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>مشاوره و تدریس پاره ای از مهارتهای تخصصی فنی و اجرایی در سازمانهای متقاضی</div>
                        <div className="text-more"><img src={CheckImage} alt=""/>رساله دکترا در زمینه بهره وری منابع انسانی قضایی</div>
                        <div className="ovalButton-more Ali" onClick={onAliClick}>بازگشت به صفحه اصلی</div>
                        
                        </div> </div> </div>

                    </div>) : (<div>

                        <AliImage className="images-team" />
                        <div className="text-team-name one">علی</div>
                        <div className="text-team-name two">محمد حسینی</div>
                        <div className="text-team"><br /></div>
                        <div className="text-team text1" style={{color: "#527a7a"}}>عضو هسته اصلی</div>
                        <div className="text-team"><br /></div>
                        <div className="text-team text2">دکتری</div>
                        <div className="text-team text3">مدیریت منابع انسانی</div>
                        <div className="text-team"><br /></div>
                        <div className="text-team text4">کارشناسی ارشد</div>
                        <div className="text-team text5">مدیریت فناوری اطلاعات</div>
                        {/* <div className="ovalButton Ali">اطلاعات بیشتر</div> */}
                        <div className="ovalButton Ali" onClick={onAliClick}>اطلاعات بیشتر</div>

                    </div>)}

                </div>)}

                </div>

                <div className={infoClassMohsen}>
                {/* <div className="mohsen_area Rectangle"> */}

                {isMoreSara || isMoreAli ? "" : (<div>
                    {isMoreMohsen? (<div>

                        <div class="outer-container"> <div class="inner-container"> <div class="element">

                        <MohsenImage className="image-more" />
                        <div className="title-more-mohsen">محمدمحسن آسمان منظر</div>
                        <div className="title-more2-mohsen">دانشجوی دکتری مهندسی کامپیوتر</div>
                        <div className="text-more"><br /></div>
                        <div className="text-more-mohsen"><img src={CheckImage} alt=""/>دانشجوی دکتری مهندسی کامپیوتر، نرم‌افزار در دانشگاه صنعتی شریف با پذیرش بدون آزمون از طرف بنیاد نخبگان</div>
                        <div className="text-more-mohsen"><img src={CheckImage} alt=""/>کارشناسی و کارشناسی ارشد مهندسی کامپیوتر، نرم‌افزار از دانشگاه‌های علم و صنعت و صنعتی شریف</div>
                        <div className="text-more-mohsen"><img src={CheckImage} alt=""/>رتبه‌ی اول در مطقع کارشناسی و پذیرش بدون آزمون در مقطع کارشناسی ارشد و همچنین معدل بالا در این دو مقطع</div>
                        <div className="text-more-mohsen"><img src={CheckImage} alt=""/>سابقه‌ی چندین ترم کمک‌ مدرس در دانشگاه صنعتی شریف و دانشگاه علم و صنعت</div>
                        <div className="text-more-mohsen"><img src={CheckImage} alt=""/>مدل‌سازی و تحلیل کارایی سامانه‌های ابری و کلان‌داده، به ترتیب به عنوان پایان‌نامه‌ی کارشناسی و کارشناسی ارشد</div>
                        {/* <div className="text-more-mohsen"><img src={CheckImage} alt=""/>علاقه‌مند پژوهش در حوزه‌ی ارزیابی کارایی سیستم‌های کامپیوتری، سیستم‌های توزیع‌شده، سیستم‌های کلان‌داده و محاسبات ابری</div> */}
                        <div className="text-more-mohsen"><img src={CheckImage} alt=""/>سابقه‌ی کار با زبان‌های برنامه‌نویسی متفاوت (مانند گو و پایتون) و چارچوب‌های متنوع در زمینه توسعه‌ی نرم‌افزار سمت سرور</div>
                        <div className="text-more-mohsen"><img src={CheckImage} alt=""/>سابقه‌ی طراحی معماری سیستم و همچنین سابقه‌ی کار طولانی مدت با ابزار‌های مانیتورنگ و ابزار‌های اورکستریشن</div>
                        <div className="text-more-mohsen"><img src={CheckImage} alt=""/>سابقه‌ی چندین سال توسعه‌ی نرم‌افزار برای سیستم‌عامل iOS، بک‌اند و فرانت‌اند</div>
                        <div className="text-more-mohsen"><img src={CheckImage} alt=""/>سابقه‌ی چندین سال توسعه‌ی بازی تحت موتور بازی سازی یونیتی</div>
                        {/* <div className="text-more-mohsen"><img src={CheckImage} alt=""/>اطلاعات بیشتر در سایت شخصی به نشانی www.asemanmanzar.ir</div> */}
                        <div className="text-more-mohsen"><img src={CheckImage} alt=""/> اطلاعات بیشتر در سایت شخصی به نشانی<a href="https://www.asemanmanzar.ir" className="siteMohsen">asemanmanzar.ir</a></div>
                        <div className="ovalButton-more Mohsen" onClick={onMohsenClick}>بازگشت به صفحه اصلی</div>

                        </div> </div> </div>

                    </div>) : (<div>

                    <MohsenImage className="images-team" />
                    <div className="text-team-name one">محمد محسن</div>
                    <div className="text-team-name two">آسمان منظر</div>
                    <div className="text-team"><br /></div>

                    <div className="text-team text1" style={{color: "#527a7a"}}>عضو هسته اصلی</div>
                    <div className="text-team"><br /></div>

                    <div className="text-team text2">دانشجوی دکتری</div>
                    <div className="text-team text3">مهندسی کامپیوتر، نرم افزار</div>
                    <div className="text-team text4">دانشگاه صنعتی شریف</div>
                    {/* <a href="https://www.asemanmanzar.ir" className="ovalButton Mohsen">اطلاعات بیشتر</a> */}
                    <div className="ovalButton Mohsen" onClick={onMohsenClick}>اطلاعات بیشتر</div>

                    </div>)}
                </div>)}

                </div>

            </div>

            {/* </div> */}

        </div>



    );
}

