import React from 'react';
import HomeImage from './Home.png'
import TextLoop from 'react-text-loop'

import { ReactComponent as Image0 } from './emoji/0-be-asani.svg';
import { ReactComponent as Image1 } from './emoji/1-ba-sooraat.svg';
import { ReactComponent as Image2 } from './emoji/2-hooshmandane.svg';
import { ReactComponent as Image3 } from './emoji/3-eghtesadi.svg';
import { ReactComponent as Image4 } from './emoji/4-elmi.svg';
import { ReactComponent as Image5 } from './emoji/5-ba-movafaghiat.svg';
import { ReactComponent as Image6 } from './emoji/6-ghavi.svg';
import { ReactComponent as Image7 } from './emoji/7-motabegh-meil.svg';
import { ReactComponent as Image8 } from './emoji/8-herfeii.svg';

import './Home.scss';

export default function Home() {

    const infoL2 = [
        {
            name: "به آسانی",
            image: Image0
        },
        {
            name: "با سرعت",
            image: Image1
        },
        {
            name: "هوشمندانه",
            image: Image2
        },
        {
            name: "اقتصادی",
            image: Image3
        },
        {
            name: "علمی",
            image: Image4
        },
        {
            name: "با موفقیت",
            image: Image5
        },
        {
            name: "با قدرت",
            image: Image6
        },
        {
            name: "مطابق میل",
            image: Image7
        },
        {
            name: "حرفه‌ای",
            image: Image8
        }
    ]

    const l1 = "پروژه‌ی خود را"
    // const l1 = "پروژه‌ی خود را با پروگرمرز۲۴"
    // const l2 = "به آسانی"
    const l3 = "پیش ببرید"

    return (
        <div className="page home">
            <div className="bg-light-arc" />
            <img src={HomeImage} alt="" className="bg-image" />
            <div className="info-container">
                <div className="info">{l1}</div>
                <div className="info loop-controller">
                    <TextLoop>
                        {
                            infoL2.map((elem, index) =>
                                (<div className="loop-item" key={"emoji-" + String(index)}>
                                    <div className="title">
                                        {elem.name}
                                    </div>
                                    {React.createElement(elem.image, { className: "image" })}
                                </div>)
                            )
                        }
                    </TextLoop>
                </div>
                <div className="info">{l3}</div>
            </div>
        </div>
    );
}

